* {
  font-family:  Helvetica, Arial, sans-serif;
  box-sizing: border-box;
}

body, html{
  padding: 0;
  margin: 0;
  display: flex;
}

header {
  float: left;
  width: 100%;
  background: #00264a;
  padding: 0 60px;
  box-sizing: border-box;
}

header h1 {
  width: 100px;
  float: left;
  margin: 0.6em 0 0.3em 0;
}

header h1 img {
  width: 100%;
}

header h2 {
  color: #fff;
  font-size: 3rem;
  margin: 25px 0 0;
  display: inline-block;
  width: calc(100% - 100px);
  padding: 0 0 0 20px;
}

header h4 {
  color: #fff;
  font-size: 1.5rem;
  margin: 0;
  display: inline-block;
  width: calc(100% - 100px);
  padding: 0 0 0 20px;
}

form {
  padding: 20px 20px 5px;
  display: flow-root;
}

input{
  width: 50%;
  font-size: 1rem;
  padding: 10px;
  color: #777;
  font-weight: 100;
  border: 1px solid #d0d0d0;
  border-radius: 3px;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0 20px;
}

.card-grid .card{
  border: 1px solid #d0d0d0;
  overflow: hidden;
  border-radius: 3px;
  width: 100%;
  padding: 15px;
  display: -webkit-box;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 6px;
}

.card-grid .card picture {
  width: 150px;
  height: 150px;
  display: inherit;
  -webkit-box-flex: 0;
  border-radius: 100%;
  overflow: hidden;
  border: 1px solid #e0e0e0;
}

.card-grid .card picture img {
  width: 100%;
  text-align: center;
}

.card-grid .card .info-wrapper {
  -webkit-box-flex: 0;
  flex: 0 0 75%;
  padding: 0 20px;
  box-sizing: border-box;
}

.card-grid .card .info-wrapper h2 {
  margin: 0 0 10px;
  padding-bottom:  10px;
  color: #4788c7;
  border-bottom: 1px solid;
  font-size: 1.2rem;
}

.card-grid .card .info-wrapper .info{
  padding-left: 10px;
}

.card-grid .card .info-wrapper .info label{
  color: #4788c7;
  font-size: 0.8rem;
  font-weight: 700;
  display: inline-block;
  margin-right: 6px;
}

.card-grid .card .info-wrapper .info p{
  margin: 0;
  color: #777;
  font-size: 0.8rem;
  display: inline-block;
}

.card-grid .card .info-wrapper .info.description label,
.card-grid .card .info-wrapper .info.description p{
  display: block;
  margin-top: 10px;
}